let html = document.querySelector('#html');
let style = document.querySelector('#style');
let div1 = document.querySelector('#div1')
let div1Wrapper = document.querySelector('#div1wrapper')
let height = document.documentElement.clientHeight
let string = `/*ä½ å¥½ï¼Œæˆ‘å«å°èµµ
*æŽ¥ä¸‹æ¥æˆ‘è¦æ¼”ç¤ºæˆ‘çš„å‰ç«¯åŠŸåº•
*é¦–å…ˆæˆ‘è¦å‡†å¤‡ä¸€ä¸ª div
*/
#div1{
    border: 1px solid red;
    width:200px;
    height:200px;
}
/*æŽ¥ä¸‹æ¥æˆ‘æŠŠ div å˜æˆä¸€ä¸ªå…«å¦å›¾
*æ³¨æ„çœ‹å¥½äº†
*é¦–å…ˆï¼ŒæŠŠ div å˜æˆä¸€ä¸ªåœ†
*/
#div1{
    border-radius:50%;
    box-shadow:0 0 3px rgba(0,0,0,0.5);
    border:none;
}
/*å…«å¦æ˜¯é˜´é˜³å½¢æˆçš„
*ä¸€é»‘ä¸€ç™½
*/
#div1{
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/*åŠ ä¸¤ä¸ªç¥žç§˜çš„å°çƒ*/
#div1::before{
    width:100px;
    height:100px;
   top:0;
   left:50%;
   transform:translateX(-50%);
   background:black;
   border-radius:50%;
   background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
}
#div1::after{
    width:100px;
    height:100px;
    bottom:0;
    left:50%;
    transform:translateX(-50%);
    background:white;
    border-radius:50%;
    background: radial-gradient(circle, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 25%, rgba(255,255,255,1) 100%);
}
`;
let string2 = "";
let n = 0;
let step = () => {
    setTimeout(() => {
        if (string[n] === "\n") {
            string2 += "</br>"
        } else if (string[n] === " ") {
            string2 += "&nbsp"
        } else {
            string2 += string[n]
        }
        html.innerHTML = string2;
        style.innerHTML = string.substring(0, n);
        window.scrollTo(0, height);
        html.scrollTo(0, height);
        if (n < string.length - 1) {
            n = n + 1;
            step();
        }
    }, 10);
}
step();
if ("ontouchstart" in window) {
    html.style.height = "70vh"
    html.style.overflow = "auto"
    div1Wrapper.style.height = "30vh"
    div1.style.position = 'relative'
    div1.style.bottom = '100%'
    document.body.style.overflow = 'hidden'
}